.header{
    display: flex;
    justify-content: space-between;
    background-color: #F6F7FF;
    padding: 34px 100px;
}

.header .logo{
    display: flex;
    align-items: center;
}

.header .logo img{
    width: 23px;
    height: 23px;
    margin-right: 15px;
}

.header .logo h2{
    color: #1F1E25;
font-family: 'Open Sans';
font-size: 20px;
font-style: normal;
font-weight: 700;
line-height: normal;
margin-right: 80px;
}


.header .cabinet{
    display: flex;
    align-items: flex-end;
}

.header .cabinet img{
    width: 20px;
    height: 22px;
    margin-right: 15px;
}

.header .cabinet p{
    color: #1F1E25;
font-family: 'Roboto';
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: normal;

}